  box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.6);
  background-color: white;
  width: 100%;
  position: fixed;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 1000;
}

header nav {
  position: relative;
  height: 8vh;
  display: flex;
}

header nav section img {
  height: 8vh;
  padding: 5px 20px;
  cursor: pointer;
}

header nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
  position: absolute;
  right: 0;
}

header nav ul li {
  width: 120px;
  height: 100%;
  color: #555;
  font-size: 1em;
  position: relative;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  display: inline-block;
}

header nav ul li:hover {
  background-color: #f7f7f7;
  color: #0099ff;
}

header nav ul li:hover:nth-of-type(3) {
  background-color: transparent;
  color: #555;
}

header nav ul li:hover:nth-of-type(3)::before {
  width: 100%;
}

header nav ul li:hover:nth-of-type(3)::after {
  width: 0;
  right: 0;
}

header nav ul li span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

header nav ul .auth-link-item {
  display: inline-block;
}

header nav ul .auth-link-item::before {
  content: "";
  position: absolute;
  height: 4px;
  width: 0;
  right: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

header nav ul .auth-link-item::after {
  content: "";
  position: absolute;
  height: 4px;
  width: 100%;
  right: 0;
  bottom: -1px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

header nav ul .auth-link-item:hover {
  background-color: transparent;
  color: #555;
}

header nav ul .auth-link-item:hover::before {
  width: 100%;
}

header nav ul .auth-link-item:hover::after {
  width: 0;
  right: 0;
}

