  left: 0;
  top: 2px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.authentication-form form div input {
  font-size: 15px;
  background: transparent;
  color: #222;
  width: 100%;
  padding-left: 25px;
  border: none;
  border-bottom: 1px solid #ccc;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  letter-spacing: 0.2px;
}

.authentication-form form div input:focus {
  outline: none;
  border-bottom: 1px solid #0099ff;
}

.authentication-form form div input:focus + label {
